            Home
          </v-subheader>
        </v-flex>
        <v-flex xs6 class="text-xs-center">
          <router-link to="/" class="body-2 black--text">Home</router-link>
        </v-flex>
      </v-layout>
      
      <v-layout
        row
        align-center>
        <v-flex xs6>
          <v-subheader>
            About
          </v-subheader>
        </v-flex>
        <v-flex xs6 class="text-xs-center">
          <router-link to="/about" class="body-2 black--text">About</router-link>
        </v-flex>
      </v-layout>

      <!-- TODO: fix incorrect navigation drawer menu entries -->
      <!--<v-list-group
        v-model="item.model"
        :prepend-icon="item.model ? item.icon : item['icon-alt']"
        append-icon="">
        <v-list-tile slot="activator">
          <v-list-tile-content>
            <v-list-tile-title>Home</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

      </v-list-group>-->

