  <!--<v-footer app>
    <v-toolbar
      color="green darken-3"
      dark
      flat
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      fixed>
    <v-toolbar-side-icon></v-toolbar-side-icon>
    <v-toolbar-title>(c) 2018</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items class="hidden-sm-and-down">
      <v-btn flat>GitHub</v-btn>
      <v-btn flat>Medium</v-btn>
      <v-btn flat>Twitter</v-btn>
    </v-toolbar-items>
  </v-toolbar>
  </v-footer>-->
